.weui-tabbar {
    height: 1rem;
    line-height: 1;

    background: #ffffff;
    box-shadow: 0px 0px 9px rgba(53, 53, 53, 0.15); // inset
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
    svg {
        display: inline-block;
        height: 0.4rem;
        margin: 0 auto;
        width: 0.4rem;
    }
    .icon-active {
        display: none;
    }
    .weui-tabbar__item {
        padding: 0.15rem 0;
        color: #5f6379;
    }
    .tabicon {
        width: auto;
        height: auto;
    }
    p {
        font-size: 0.25rem;
        color: #5f6379;
    }
    .weui-tabbar__item.router-link-active {
        background-color: inherit;
        color: #3688ff;
        .icon {
            display: none;
        }
        .icon-active {
            display: block;
        }
        p {
            color: #3688ff;
        }
    }
}
</style>



